<template>
  <div class="twLogin">
    <div v-if="isBindPhoneOk" class="bindphone-ok">
      <svg viewBox="0 0 20 20" fill="currentColor" class="bindphone-ok__icon w-6 h-6"><path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clip-rule="evenodd"></path></svg>
      <p class="bindphone-ok__text">绑定成功</p>
      <button class="btn2 bindphone-ok__btn" @click="toAdminHome">
        确定
      </button>
    </div>
    <div v-else-if="showBindPhone">
      <h3 class="tw-bindphone__title">绑定手机号</h3>
      <p class="tw-bindphone__info">尊敬的用户，为了保证您账号的安全性，请绑定您的个人手机号～</p>
      <div class="row bindphone-input-item">
        <input v-model="form.phone" name="phone" placeholder="请输入手机号" type="text" />
        <div class="tips">
          <div class="item">
            <img class="phoneWrong" src="../assets/images/sign_icon_wrong.png" />
            <span>请输入正确的手机号</span>
          </div>
        </div>
      </div>
      <div class="row bindphone-input-item get-code">
        <div class="get-code-input">
          <input class="" v-model="this.form.code" name="code" placeholder="请输入验证码" type="text" />
        </div>
        <button @click="getAuthCode2" class="btn2" :disabled="block">{{ block ? countdown : '获取验证码' }}</button>
      </div>
      <div class="click_login btn2" v-on:click="bindPhone">确定绑定</div>
    </div>
    <div v-else>
      <ul class="login-switch">
        <li v-on:click="goUserLogin">用户登录</li>
        <li>团委登录</li>
      </ul>
      <form @submit.prevent="doLogin" class="tw-login-form">
        <div class="row form-item">
          <svg viewBox="0 0 20 20" fill="currentColor" class="form-icon w-6 h-6"><path fill-rule="evenodd" d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z" clip-rule="evenodd"></path></svg>
          <input @blur="handleBlur" placeholder="请输入账号" class="form-input" id="tw-login-username" minlength="1" name="username" type="text" />
          <!-- <label for="tw-login-username" class="form-text">请输入账号</label> -->
        </div>
        <div class="row form-item" >
          <svg viewBox="0 0 20 20" fill="currentColor" class="form-icon w-6 h-6"><path fill-rule="evenodd" d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z" clip-rule="evenodd"></path></svg>
          <input name="password" class="form-input" placeholder="请输入密码" type="password" />
        </div>
        <button class="click_login btn2">登录</button>
        <div class="login_tips">
          登录即表示同意
          <router-link to="/tos">《使用条款》</router-link>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import { adminGetCode, adminBindPhone, adminLogin } from "@/api/admin";
export default {
  data() {
    return {
      isBindPhoneOk: false,
      showBindPhone: false,
      form: {
        phone: "",
        code: "0000",
      },
      block: false,
      coundown: 0
    };
  },
  methods: {
    toAdminHome() {
      window.location = "/tuanwei/home";
    },
    handleBlur(e) {
      let v = e.target.value;
    },
    getAuthCode2() {
      let phone = this.form.phone;
      adminGetCode({
        phone,
      }).then((res) => {
        if (+res.data.code === 200) {
          this.block = true;
          this.countdown = 5;
            let tick = () => {
              this.countdown -= 1;
              console.log(this.countdown);
              if (this.countdown <= 0) {
                this.block = false;
              } else {
                setTimeout(() => {
                  tick();
                }, 1000);
              }
            }
            setTimeout(() => {
              tick();
            }, 1000);
        }
        console.log("res", res);
      });
    },
    bindPhone() {
      let phone = this.form.phone;
      let code = this.form.code || "0000";
      adminBindPhone({
        code,
        phone,
      }).then((res) => {
        console.log("res", res);
        if (+res.data.code === 200) {
          this.isBindPhoneOk = true;
        }
      });
    },
    doLogin(e) {
      let form = e.target;
      let fd = new FormData(form);
      let username = fd.get("username");
      let password = fd.get("password");
      adminLogin({
        username,
        password,
      })
        .then((res) => {
          // this.showBindPhone = true;
          // return;
          let { phone } = res.data.data.admin;
          if (!phone) {
            this.showBindPhone = true;
          } else if (res.data.code === 200) {
            window.location = "/tuanwei/home";
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
    goUserLogin() {
      this.$router.push("/UserLogin");
    },
  },
};
</script>
