import {
  $http
} from '@/assets/plugin/axios.js';
export const adminGetCode = ({
  phone
}) => {
  return $http.get(`/proposal/admin/api/v1/admin/my/phone/code/${phone}`);
};
export const adminBindPhone = ({
  code,
  phone
}) => {
  return $http.patch(`/proposal/admin/api/v1/admin/my/phone`, {
    phone,
    code
  });
};


export const adminLogin = ({
  username,
  password
}) => {
  return $http.post('/proposal/admin/api/v1/auth', {
    username,
    password
  });
};

export const adminLogout = () => {
  return $http.delete('/proposal/admin/api/v1/auth');
};

export const getGenderRatio = () => {
  return $http.get('/proposal/admin/api/v1/dashboard/competitor/gender')
};

export const getDailyApply = ({
  startDate,
  endDate
}) => {
  return $http.get('/proposal/admin/api/v1/dashboard/competitor/newJoin/daily', {
    params: {
      startDate,
      endDate
    }
  })
}

export const getJoinType = () => {
  return $http.get('/proposal/admin/api/v1/dashboard/competitor/joinType')
}

export const getLevelRatio = () => {
  return $http.get('/proposal/admin/api/v1/dashboard/competitor/level')
}

export const getPoliticsStatus = () => {
  return $http.get('/proposal/admin/api/v1/dashboard/competitor/politics')
}

export const getApplyRank = ({
  size
}) => {
  return $http.get('/proposal/admin/api/v1/dashboard/competitor/city/top/' + size)
}

export const getAdminProfile = () => {
  return $http.get('/proposal/admin/api/v1/admin/my/profile')
}


export const updateAdminProfile = (obj = {
  realName,
  phone,
  sex
}) => {
  return $http.patch('/proposal/admin/api/v1/admin/my/profile', obj)
}
